import React, { Component } from 'react'
import { TimelineLite, Expo } from 'gsap';
import {connect} from 'react-redux';

import './CreateChat.css'

import {getAllUser} from '../../Redux/actions/user';
import {createChat, getChats} from '../../Redux/actions/chats';

class CreateChat extends Component {

    constructor(props) {
        super(props);

        this.tl = new TimelineLite({paused: true, onComplete: () =>{
            this.setState({newChatOpen: true})
        }})
        this.createBtn = React.createRef()
    }

    state = {
        newChatOpen: false,
        searchInput: "",
        groupInput: "",
        checkedUser: [],
        isGroup: false,
        error: undefined,
    }

    componentDidMount() {
        this.props.getAllUser()
        this.prepareNewChatAnim();
    }

    prepareNewChatAnim = () => {

        let {tl} = this;
        let obj = this.createBtn.current;

        let child = obj.childNodes[0]

        tl.to(child, .25, {opacity: 0, display: "none"});

        tl.to(obj, .5, {
            width: "100%",
            height: "100vh",
            left: "0%",
            bottom: 0,
            borderRadius: 0,
            delay: 0,
            backgroundColor: "white",
            ease: Expo.easeInOut,
            display: "block",
            cursor: "auto",
        })
    }

    newChat = () => {
        if(!this.state.newChatOpen) {
            this.tl.play();
            this.setState({checkedUser: [], searchInput: "", groupInput: "", isGroup: false, error: undefined})
        }
    }

    closeNewChat = () => {
        this.tl.reverse();
        this.setState({newChatOpen: false})
    }

    handleInput = (item) => (e) => {
        this.setState({[item]: e.target.value})
    }

    handleOnChange = (user) => (e) => {
        let checked = e.target.checked;
        const {checkedUser} = this.state;

        if(checked){
            this.setState({
                checkedUser: [...checkedUser, user]
            }, () => {
                if(this.state.checkedUser.length > 1){
                    this.setState({isGroup: true})
                }
            })
        }else {
            let array = [...checkedUser];
            let index = array.indexOf(user);
            if (index > -1) {
                array.splice(index, 1);
            }
            this.setState({checkedUser: array}, () => {
                if(this.state.checkedUser.length < 2){
                    this.setState({isGroup: false})
                }
            });
        }
    }

    handleSubmit = (e) => {
        e.preventDefault();

        const {checkedUser, groupInput} = this.state;

        if(checkedUser.length > 1){

            if(groupInput.length < 3){
                return this.setState({error: "You must type in a longer groupname."})
            }

            if(groupInput.length > 30) {
                return this.setState({error: "Your groupname is too long."})
            }

            this.props.createChat(this.getUsernames(checkedUser), groupInput)
                .then(_ => {
                    this.props.getChats()
                })
            this.closeNewChat()

        }else if(checkedUser.length === 1) {
            this.props.createChat(this.getUsernames(checkedUser))
                .then(_ => {
                    this.props.getChats();
                })
            this.closeNewChat()
        }
    }

    handleGroupSubmit = () => {
        const {groupInput, checkedUser} = this.state;
        if(groupInput.length < 3){
            this.setState({groupError: true})
        }else {
            this.props.createChat(this.getUsernames(checkedUser), groupInput)
                .then(e => {
                    this.props.toggle();
                })
        }
    }

    getUsernames = (arr) => {
        return arr.map(item => item.username)
    }

  render() {
    const {newChatOpen, searchInput, isGroup, error} = this.state;
    const {allUsers, user} = this.props;
    const users = allUsers.filter(item => item.username !== user.username)
    const allUsersFilter = users.filter(user => user.username.toLowerCase().includes(searchInput.toLowerCase()))
    return (
        <div ref={this.createBtn} onClick={this.newChat} className="chatListNewChatBtn">
            <p>New Chat</p>
            {
                newChatOpen ? 
                <div>
                    <div className="newChatHeader">
                        <div>
                            <h1>New Chat</h1>
                            <svg onClick={this.closeNewChat} width="24" height="24">
                                <path d="M19 6.4L17.6 5 12 10.6 6.4 5 5 6.4l5.6 5.6L5 17.6 6.4 19l5.6-5.6 5.6 5.6 1.4-1.4-5.6-5.6z"/>
                                <path fill="none" d="M0 0h24v24H0z"/>
                            </svg>
                        </div>
                        <input 
                            placeholder="Search"
                            onChange={this.handleInput("searchInput")}
                            value={searchInput}
                        />
                        {isGroup
                        ?
                            <input 
                                placeholder="Groupname"
                                onChange={this.handleInput("groupInput")}
                            />
                        :
                            <div></div>
                        }
                        {
                            error ?
                            <p>{error}</p>
                            :
                            <div></div>
                        }
                    </div>
                    <div className="newChatUserContainer">
                        {
                            allUsersFilter.map((user,i) => (
                                <div className="newChatUser" key={i}>
                                    <div>
                                        <h3>{user.username}</h3>
                                        <p>{user.email}</p>
                                    </div>
                                    <input onChange={this.handleOnChange(user)} type="checkbox"/>
                                </div>
                            ))
                        }
                        <div className="newChatSpace"></div>
                    </div>
                    <div onClick={this.handleSubmit} className="newChatFab">
                        <svg fill="#fff" width="24" height="24">
                            <path d="M3 17.3V21h3.8l11-11-3.7-3.8L3 17.2zM20.7 7c.4-.4.4-1 0-1.4l-2.3-2.3a1 1 0 0 0-1.4 0L15 5 19 9 20.7 7z"/>
                        </svg>
                    </div>
                </div>
                :
                <div></div>
            }
        </div>
    )
  }
}

const mapStateToProps = state => {
    return {
        allUsers: state.allUsers,
        user: state.user
    }
}

export default connect(mapStateToProps, {createChat, getAllUser, getChats})(CreateChat)